import React from 'react';
import {connect} from 'react-redux';

const Counter = props => {
    return (
        <div>
            <h1> I am counter </h1>
            <p>Count: {props.count}</p>
            <button onClick={props.onIncrementClick}>increment</button>
            <button onClick={props.onDecrementClick}>decrement</button>
        </div>
    );
}

const mapStateToProps = (state) => {
    return {
        count: state.count
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        onIncrementClick: () => { /**all actions are object with type property */
            const action = {type: 'INCREMENT'}
            dispatch(action);   
        },
        onDecrementClick: () => {
            console.log('mmm');
            const action = {type: 'DECREMENT'}
            dispatch(action); 
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Counter);//connect our mapState func to Counter component with react-redux