import React from 'react';
import {connect} from 'react-redux';

const Mirror = props => {
    return(
        <div>
            <input value={props.inputVal} onChange={props.onChange}/>
            <p>{props.inputVal}</p>
        </div>
    )
}

const mapStateToProps = (state) => {
    return {
        inputVal: state.inputVal
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        onChange: (event) => { /**all actions are object with type property */
            const action = {type: 'CHANGETEXT', text: event.target.value}
            dispatch(action);   
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Mirror);//connect our mapState func to Counter component with react-redux