/*create reducers run*/
import {createStore} from 'redux';

const initialState = {
    count: 0,
    inputVal: ''
};


const reducer = (state = initialState, action) => {
    console.log('reducer fx in state', action)
    switch(action.type){
        case 'INCREMENT':
            return Object.assign({}, state, {count: state.count + 1});
        case 'DECREMENT':
            return Object.assign({}, state, {count: state.count - 1});
        case 'CHANGETEXT':
            return Object.assign({}, state, {inputVal: action.text});
        default:
            return state;
    }
}

const store = createStore(reducer);

export default store;